* {
  direction: rtl;
  text-align: right;
}
body {
  background-color: #f6f6f6 !important;

  margin: 0;
  font-family: "Vazir", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  color: #000;
  transition: all 0.5s ease;
}
a:hover,
a:focus {
  color: #6777ef;
  text-decoration: none;
}
.text-light-2 {
  color: rgba(85, 85, 86, 0.8) !important;
}
.display-block-JQ {
  display: block !important;
}
.badge-primary,
.bg-primary {
  background-color: #6777ef !important;
}
.badge-info,
.bg-info {
  background-color: #3abaf4 !important;
}
.badge-success,
.bg-success {
  background-color: #54ca68 !important;
}
.badge-danger,
.bg-danger {
  background-color: #fc544b !important;
}
.bg-warning {
  background-color: #ff9800 !important;
}
.bg-purple {
  background-color: #9c27b0 !important;
  color: #fff;
}
.btn-primary {
  background-color: #6777ef !important;
}
.btn-primary:hover {
  background-color: #4f5ece !important;
}
.btn-facebook {
  color: #fff;
  background-color: #3b5998;
  border: none;
  direction: ltr;
}
.btn-github {
  color: #fff;
  background-color: #444444;
  border: none;
  direction: ltr;
}
.btn-instagram {
  color: #fff;
  background-color: #4b6294;
  border: none;
  direction: ltr;
}
.btn-twitter {
  color: #fff;
  background-color: #55acee;
  border: none;
  direction: ltr;
}
.btn-twitter:hover,
.btn-facebook:hover,
.btn-github:hover,
.btn-instagram:hover {
  color: #fff;
}
.btn-outline-primary {
  border-color: #6777ef;
  color: #6777ef;
}
.btn-outline-primary.disabled,
.btn-outline-primary:disabled {
  border-color: #6777ef;
  color: #6777ef;
}
.btn-outline-primary.disabled:hover,
.btn-outline-primary:disabled:hover {
  border-color: #6777ef;
  color: #fff;
}
.btn-outline-primary:hover {
  background-color: #6777ef !important;
}
#main {
  transition: all 0.5s ease;
  -webkit-box-shadow: inset -30px 30px 50px -55px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: inset -30px 30px 50px -55px rgba(0, 0, 0, 0.75);
  box-shadow: inset -30px 30px 50px -55px rgba(0, 0, 0, 0.75);
}
.fa-times-circle,
.cursor-pointer {
  cursor: pointer;
}
/* ------------------------------------
              Toastify
--------------------------------------*/
.Toastify__toast {
  font-family: "Vazir", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* ------------------------------------
             Login Form
--------------------------------------*/
.form-login i {
  font-size: 60px;
  color: #6777ef;
  border: 2px solid #6777ef;
  padding: 25px;
  border-radius: 100%;
}
.form-login .card {
  border: 1px solid #6777ef;
  box-shadow: 0 0.46875rem 2.1875rem rgba(103, 119, 239, 0.5),
    0 0.9375rem 1.40625rem rgba(103, 119, 239, 0.5),
    0 0.25rem 0.53125rem rgba(103, 119, 239, 0.5),
    0 0.125rem 0.1875rem rgba(103, 119, 239, 0.5);
}
/* ------------------------------------
             Register Form
--------------------------------------*/
.border-gray {
  border-color: #b0b0b0 !important;
}
.color-lightblue {
  color: rgb(72, 166, 234) !important;
}
.form-control:focus,
.form-control:hover {
  color: #6777ef;
  border: 1px solid #6777ef !important;
}
/* ------------------------------------
            Custom Input
--------------------------------------*/
.form-control.is-invalid {
  background-position: left calc(0.375em + 0.1875rem) center;
  padding-right: 0.75rem;
  padding-left: calc(1.5em + 0.75rem);
}
.custom-file-label::after {
  right: unset;
  left: 0;
  content: "جستجو";
}
label {
  color: #60686f;
  font-size: 14px;
}
/* ------------------------------------
            Navbar (top)
--------------------------------------*/
.navbar {
  -webkit-box-shadow: -240px 30px 41px -35px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -240px 30px 41px -35px rgba(0, 0, 0, 0.75);
  box-shadow: -240px 30px 41px -35px rgba(0, 0, 0, 0.75);
}
.navbar-nav .nav-item {
  font-size: 18px;
}
.navbar-nav img {
  height: 30px;
}
.navbar-nav .user-profile-icon {
  border-radius: 5px;
  box-shadow: 4px 3px 6px 0 rgba(0, 0, 0, 0.2);
}
.bell {
  -webkit-animation: ring 4s 0.7s ease-in-out infinite;
  -webkit-transform-origin: 50% 4px;
  -moz-animation: ring 4s 0.7s ease-in-out infinite;
  -moz-transform-origin: 50% 4px;
  animation: ring 4s 0.7s ease-in-out infinite;
  transform-origin: 50% 4px;
}

@-webkit-keyframes ring {
  0% {
    -webkit-transform: rotateZ(0);
  }
  1% {
    -webkit-transform: rotateZ(30deg);
  }
  3% {
    -webkit-transform: rotateZ(-28deg);
  }
  5% {
    -webkit-transform: rotateZ(34deg);
  }
  7% {
    -webkit-transform: rotateZ(-32deg);
  }
  9% {
    -webkit-transform: rotateZ(30deg);
  }
  11% {
    -webkit-transform: rotateZ(-28deg);
  }
  13% {
    -webkit-transform: rotateZ(26deg);
  }
  15% {
    -webkit-transform: rotateZ(-24deg);
  }
  17% {
    -webkit-transform: rotateZ(22deg);
  }
  19% {
    -webkit-transform: rotateZ(-20deg);
  }
  21% {
    -webkit-transform: rotateZ(18deg);
  }
  23% {
    -webkit-transform: rotateZ(-16deg);
  }
  25% {
    -webkit-transform: rotateZ(14deg);
  }
  27% {
    -webkit-transform: rotateZ(-12deg);
  }
  29% {
    -webkit-transform: rotateZ(10deg);
  }
  31% {
    -webkit-transform: rotateZ(-8deg);
  }
  33% {
    -webkit-transform: rotateZ(6deg);
  }
  35% {
    -webkit-transform: rotateZ(-4deg);
  }
  37% {
    -webkit-transform: rotateZ(2deg);
  }
  39% {
    -webkit-transform: rotateZ(-1deg);
  }
  41% {
    -webkit-transform: rotateZ(1deg);
  }
  43% {
    -webkit-transform: rotateZ(0);
  }
  100% {
    -webkit-transform: rotateZ(0);
  }
}

@-moz-keyframes ring {
  0% {
    -moz-transform: rotate(0);
  }
  1% {
    -moz-transform: rotate(30deg);
  }
  3% {
    -moz-transform: rotate(-28deg);
  }
  5% {
    -moz-transform: rotate(34deg);
  }
  7% {
    -moz-transform: rotate(-32deg);
  }
  9% {
    -moz-transform: rotate(30deg);
  }
  11% {
    -moz-transform: rotate(-28deg);
  }
  13% {
