.navbar-nav .nav-item .badge-top-right {
  position: relative;
  font-weight: 300;
  font-size: small;
  right: 10px;
  bottom: 10px;
  padding: 3px 6px;
  border-radius: 10px;
}
.dropdown-menu {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
  border: none;
  width: 200px;
  transform-origin: top;
}
.dropdown-list {
  width: 300px;
}
.dropdown-list img,
.dropdown-list i {
  flex-shrink: 0;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  line-height: 42px;
  text-align: center;
  font-size: 13px;
}
.dropdown-list .dropdown-list-content {
  height: 250px;
  overflow-y: auto;
}
.dropdown-list small {
  white-space: normal;
}
.dropdown-list span.dropdown-list-span-content span {
  font-size: 12px;
}
.dropdown-list span.dropdown-list-span-content span:last-child {
  font-size: 10px;
}
a.dropdown-footer-link,
.dropdown-list .dropdown-header a {
  color: #6777ef;
}
a.dropdown-footer-link:hover,
.dropdown-list .dropdown-header a:hover {
  text-decoration: underline;
}
.dropdown-list span.fa-angle-right {
  font-size: 14px;
}
.pulldown.show {
  animation: pulldown 1s;
}
@keyframes pulldown {
  from {
    transform: scaleY(0);
  }
  to {
    transform: scaleY(1);
  }
}
#minimize-sidebar,
#slide-to-right-sidebar {
  cursor: pointer;
}
/* ------------------------------------
               SideBar
--------------------------------------*/
#sidebar.minimized a.primary-sidebar-link {
  text-align: center;
}
#sidebar.minimized ul div a::after,
#sidebar.minimized .collapse-menu {
  display: none !important;
}
#sidebar.minimized .sidebarDropdown {
  display: flex;
}
#sidebar .sidebarDropdown {
  display: none;
}
#sidebar {
  top: 78px;
  overflow: auto;
  z-index: 1019;
}
.dropdown-list .dropdown-list-content::-webkit-scrollbar,
#sidebar::-webkit-scrollbar {
  width: 5px;
  background-color: #fff;
}
.dropdown-list .dropdown-list-content::-webkit-scrollbar-thumb,
#sidebar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(66, 66, 66, 0);
}
.dropdown-list .dropdown-list-content:hover::-webkit-scrollbar-thumb,
#sidebar:hover::-webkit-scrollbar-thumb {
  background-color: rgba(66, 66, 66, 0.8);
}
#sidebar {
  transform: translate(0, 0);
  overflow-y: auto;
  transition: all 0.5s ease, top 0s;
}
#sidebar ul a {
  font-weight: 400;
  color: #60686f;
}
#sidebar ul div a::after {
  content: "-";
  float: left;
  font-size: large;
}
#sidebar ul div a.collapsed::after {
  content: "+";
  float: left;
  font-size: large;
}
#sidebar ul a.active,
#sidebar ul a:hover {
  background-color: #f0f3ff;
  color: #6777ef;
}
#sidebar ul ul a {
  font-weight: 400;
  transition: all 0.3s ease;
  color: rgb(96, 104, 111);
}
#sidebar ul ul a::after {
  content: none;
}
#sidebar ul ul a::before {
  content: ">";
  position: relative;
  padding-left: 10px;
  padding-right: 0px;
  font-size: 10x;
  font-weight: 600;
  transition: all 0.5s ease;
}
#sidebar ul ul a:hover::before {
  padding-left: 3px;
  padding-right: 7px;
}
#sidebar ul ul a:hover {
  background-color: transparent;
  color: #000;
}
@media (max-width: 991px) {
  #sidebar {
    position: absolute;
  }

  #sidebar.slide-from-right {
    transform: translate(100%, 0);
  }
}
.dropleft .dropdown-menu {
  transform-origin: right center;
  transition: all 1s ease 0s;
}
/* ------------------------------------
               Card
--------------------------------------*/
.card {
  box-shadow: 0 0.46875rem 2.1875rem rgba(90, 97, 105, 0.1),
    0 0.9375rem 1.40625rem rgba(90, 97, 105, 0.1),
    0 0.25rem 0.53125rem rgba(90, 97, 105, 0.12),
    0 0.125rem 0.1875rem rgba(90, 97, 105, 0.1);
  border: none;
  border-radius: 10px;
}
/* ------------------------------------
               loader
--------------------------------------*/
#loader {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 1;
  width: 80px;
  height: 80px;
  margin: -75px 0 0 -75px;
  border: 8px solid #f3f3f3;
  border-radius: 50%;
  border-top: 8px solid #6777ef;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* ------------------------------------
               Pagination
--------------------------------------*/
.page-item:first-child .page-link {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
}
.page-item:last-child .page-link {
  border-top-left-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.page-link {
  color: #6777ef;
  border-color: #6777ef;
}
.page-link:hover,
.page-item.active .page-link {
  cursor: pointer;
  color: #fff;
  border-color: #6777ef;
  background-color: #6777ef;
}
/* ------------------------------------
              react-quill
--------------------------------------*/
.ql-snow .ql-picker:not(.ql-color-picker):not(.ql-icon-picker) svg {
  right: unset;
  left: 0;
}
.ql-snow .ql-editor {
  min-height: 300px;
}
.ql-snow.ql-toolbar button:hover,
.ql-snow .ql-toolbar button:hover,
.ql-snow.ql-toolbar button:focus,
.ql-snow .ql-toolbar button:focus,
.ql-snow.ql-toolbar button.ql-active,
.ql-snow .ql-toolbar button.ql-active,
.ql-snow.ql-toolbar .ql-picker-label:hover,
.ql-snow .ql-toolbar .ql-picker-label:hover,
.ql-snow.ql-toolbar .ql-picker-label.ql-active,
.ql-snow .ql-toolbar .ql-picker-label.ql-active,
.ql-snow.ql-toolbar .ql-picker-item:hover,
.ql-snow .ql-toolbar .ql-picker-item:hover,
.ql-snow.ql-toolbar .ql-picker-item.ql-selected,
.ql-snow .ql-toolbar .ql-picker-item.ql-selected {
  color: #6777ef;
  border-bottom: 1px solid #6777ef;
}
